/* Fonts */

@include fontGenerator("Nunito Regular",(400),(nunito-regular));
@include fontGenerator("Nunito Medium",(500),(nunito-medium));
@include fontGenerator("Nunito Semibold",(600),(nunito-semibold));
@include fontGenerator("Nunito Bold",(700),(nunito-bold));


@mixin font-400 {
  font-family: "Nunito Regular", sans-serif;
  font-weight: 400; }

@mixin font-500 {
  font-family: "Nunito Medium", sans-serif;
  font-weight: 500; }

@mixin font-600 {
  font-family: "Nunito Semibold", sans-serif;
  font-weight: 600; }

@mixin font-700 {
  font-family: "Nunito Bold", sans-serif;
  font-weight: 700; }
